@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Outfit', sans-serif;
}

body{
  background-color: rgb(233, 223, 223);
}

h1, h4, p,
a{
  color: white;
  text-decoration: none;
}

ul{
  list-style-type: none;
}

.btn{
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: coral;
  color: black;
  border: wheat;
  font-weight: 600;
  cursor: pointer;
}

.btn-light{
  background: transparent;
  color: wheat;
}

.btn:hover{
  background: lightseagreen;
  color: wheat;
  transition: 0.2s;
}
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}

.header-bg{
    background-color:rgb(51, 40, 40);
    transition: 0.5s;
}

.nav-menu{
    display: flex;
}

.nav-menu li{
padding: 0 1rem;
}

.nav-menu li a {
   font-size: 1.2rem;
   font-weight: 500;
}

.hamburger{
    display: none;
}

@media screen and (max-width:1040px){
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgb(51, 40, 40);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.5s;
    }

    .nav-menu.active{
        left: 0;
    }

    .nav-menu li{
        padding: 1rem 0;
    }

    .nav-menu li a{
        font-size: 2rem;
    }

    .hamburger{
        display: initial;
    }

}

.email{
    width: 40%;
    height: 40%;
    position: relative;
    left: 50%;
}

.linkdin{
    width: 40%;
    height: 40%;
}
.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}

.into-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.5;
}

.hero{
    height: 100%;
    width: 100%;
}

.hero .content{
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}

